  .rightFooter {
    /* width: 20%; */
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  
  
  .rightFooter>a {
    text-decoration: none;
    font-size: 170%;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    color: white;
    transition: all 0.5s;
    margin: 0.5vmax;
    text-align:center;
      display: inline-block;
      padding: 0.3vmax 1vmax;
      background-color: #838b99;
      display:flex;
      justify-content:center;
      align-items: center;
        border-radius: 150%;
      
      }
      
      .rightFooter>a:hover {
        color: white;
        background-color: #155cd6;
      
      }
      
      
      .newsletter {
        text-align: center;
        margin-bottom: 2rem;
      }
      
      .newsletter p {
        margin: 0;
      
      }
      
      .form-label {
        display: block;
        font-weight: bold;
        margin-bottom: 1rem;
        color: white;
      }
      
      .form-control {
        width: 100%;
        padding: 0.5rem;
        border: 2px solid #ccc;
        border-radius: 4px;
    background-color: transparent;
      color: white;
    }
    
    .btn-outline-light {
      background-color: transparent;
      color: white;
      border: 1px solid white;
      border-radius: 4px;
      padding: 0.5rem 1rem;
      cursor: pointer;
      transition: background-color 0.3s ease;
    }
    
    .btn-outline-light:hover {
      background-color: #008080;
    }
    
    
    .links {
      text-align: center;
      margin-top: 2rem;
    
      margin-bottom: 2rem;
    
    }
    
    
    .links h5 {
      font-size: 1.2rem;
      text-transform: uppercase;
      margin-bottom: 1rem;
      color: white;
    }
    
    .links ul {
      list-style-type: none;
      padding: 0;
    }
    
    .links ul li {
      margin: 0.5rem 0;
  }
  
  .links a {
    text-decoration: none;
    color: white;
    display: block;
      padding: 0.25rem 0;
      transition: color 0.3s ease;
    }
    
    .links a:hover {
      color: teal;
    }
    
    
    .links ul {
      list-style-type: none;
      padding-left: 0;
    }
    
    .links ul li::before {
      content: none;
    }
  
    .newsletter input{
      margin:0 10px;
    }
    
    input[type="email"]{
      width: 80%;
      padding:7px;
      border-radius: 5px;
      background: transparent;
      border: 1px solid white;
      
    }
    
    
    
